@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@400;500&display=swap');

body{
  font-family: 'Baloo Bhaijaan 2', cursive;
}
.col{
  width: 90%;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  min-width: 12rem;
  overflow: hidden;
  margin-top: 1rem;
  border-radius: 10px;
  cursor: pointer;
}

.col:hover{
  background-color: aliceblue;
}